import useGetPythLastPrice from '@/hooks/unstable-vault/useGetPythLastPrice'
import useTokenPrice from '@cetus/hooks/src/useTokenPrice'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { fixCoinType } from '@cetusprotocol/cetus-sui-clmm-sdk'
import { useEffect, useMemo } from 'react'
import VaultsListH5 from './H5/VaultsListH5'
import { VaultsListPC } from './PC/VaultsListPC'
import { VaultsListProps } from './type'

function VaultsList(props: VaultsListProps) {
  const { fetchTokenPrices } = useTokenPrice()
  const { dataList, isAll, showSkeletonLoading, isShowPowered } = props
  console.log('🚀🚀🚀 ~ VaultsList.tsx:13 ~ VaultsList ~ isShowPowered:', isShowPowered)
  const { getPythLastPrice } = useGetPythLastPrice()

  //获取价格
  useEffect(() => {
    console.log('🚀🚀🚀 ~ VaultsList.tsx:18 ~ useEffect ~ dataList:', dataList)
    if (dataList && dataList?.length == 0) return
    const coinTypeList: string[] = []
    const haedalCoinTypeList: string[] = []
    dataList.forEach(data => {
      if (data) {
        if (!coinTypeList.includes(data.tokenA.coin_type)) {
          coinTypeList.push(data.tokenA.coin_type)
        }

        if (!coinTypeList.includes(data.tokenB.coin_type)) {
          coinTypeList.push(data.tokenB.coin_type)
        }
        if (data.category == 'haedal') {
          haedalCoinTypeList.push(fixCoinType(data.tokenA.coin_type))
          haedalCoinTypeList.push(fixCoinType(data.tokenB.coin_type))
        }
      }
    })
    if (coinTypeList.length > 0) {
      fetchTokenPrices(coinTypeList)
      setTimeout(() => {
        getPythLastPrice([...new Set([...haedalCoinTypeList])])
      }, 500)
    }
  }, [dataList?.length])

  // if (!isAll && !currentAccount) {
  //   return <NoData mt="8px" type="nowallet" onboard={() => onWalletModal(true)} />
  // }

  // const showNoWallet = useMemo(() => {
  //   return !currentAccount
  // }, [currentAccount])

  const { isApp } = useWindowWidth()

  const host = window.location.host
  const isShowAumLimit = useMemo(() => {
    return !host.includes('app.cetus.zone') && !host.includes('cetus-cmm-pro.netlify.app') && !host.includes('localhost:5173')
  }, [host])

  return isApp ? <VaultsListH5 {...props} isShowAumLimit={isShowAumLimit} /> : <VaultsListPC {...props} isShowAumLimit={isShowAumLimit} />
}

export default VaultsList
