import { Block, CetusTooltip } from '@cetus/design'
import { formatCurrency } from '@cetus/utils'
import { Progress, Td, Text, Tr, VStack } from '@chakra-ui/react'
import React from 'react'
import CoinPairInfo from '../../../common/CoinPairInfo'
import { AumLimit } from '../../detail/AumLimit'
import { VaultsAction } from '../common/VaultsAction'
import VaultsAprBlock from '../common/VaultsAprBlock'
import { VaultsExpendItem } from '../common/VaultsExpendItem'
import VaultsRewardsBlock from '../common/VaultsRewardsBlock'
import { VaultsListItemType } from '../type'

export function VaultsListItemPC<T>({
  openExpend,
  onExpand,
  rewardPerA,
  rewardPerB,
  apiInfo,
  logo_url,
  holdCoinAmounts,
  balanceDisplay,
  sharePoolRate,
  currentAccount,
  totalValue,
  jumpVaultsDetail,
  isShowPowered,
  isShowAumLimit
}: VaultsListItemType<T>) {
  return (
    <React.Fragment key={apiInfo.vaultsId}>
      <Tr
        cursor="pointer"
        onClick={onExpand}
        sx={{
          td: {
            pb: '16px'
          }
        }}
      >
        <Td>
          <CoinPairInfo poolInfo={{ ...apiInfo, poolAddress: apiInfo?.vaultsId }} isShowPowered={isShowPowered} symbolFontSize="15px" />
        </Td>
        {isShowAumLimit ? (
          <Td textAlign="right">
            {apiInfo.hardCapUSD == 0 ? (
              <CetusTooltip
                tooltip=" No capacity limit "
                children={
                  <VStack gap="4px">
                    <Text textColor="text_caption" textAlign="right" fontWeight="500">
                      {apiInfo.vaultsTvlDisplay}
                    </Text>
                    <Text h="12px" lineHeight="12px" color="primary" bg="primary_opacity.10" p="0px 8px" borderRadius="12px">
                      ∞
                    </Text>
                  </VStack>
                }
              />
            ) : (
              <AumLimit
                depositRatio={apiInfo.depositRatio}
                hardCapUSD={apiInfo.hardCapUSD}
                vaultTvl={apiInfo.vaultsTvl}
                textStyle={{
                  textDecoration: 'none'
                }}
                value={
                  <VStack alignItems="flex-end">
                    <Text textColor="text_caption" textAlign="right" fontWeight="500">
                      {apiInfo.vaultsTvlDisplay}
                    </Text>
                    <Progress
                      h="4px"
                      w="80px"
                      value={apiInfo.depositRatio}
                      bg="primary_opacity.10"
                      sx={{
                        'div[role="progressbar"]': {
                          bg: 'primary'
                        }
                      }}
                    />
                  </VStack>
                }
              />
            )}
          </Td>
        ) : (
          <Td textAlign="right">
            <Text textColor="text_caption" textAlign="right" fontWeight="500">
              {apiInfo.vaultsTvlDisplay}
            </Text>
          </Td>
        )}

        <Td textAlign="right" w="13%">
          <VaultsAprBlock
            vaultsLstApyDisplay={apiInfo.vaultsLstApyDisplay}
            vaultsApyDisplay={apiInfo.vaultsApyDisplay}
            vaultsTotalApyDisplay={apiInfo.vaultsTotalApyDisplay}
            vaultsAprDisplay={apiInfo.vaultsAprDisplay}
            category={apiInfo.category}
          />
        </Td>
        {/* 奖励 */}
        <Td textAlign="right" w="13%">
          <VaultsRewardsBlock rewardList={apiInfo.vaultsRewards} />
        </Td>

        <Td textAlign="right">
          <Text textColor="text_caption" textAlign="right" fontWeight="500">
            {currentAccount ? formatCurrency(totalValue, 2) : '--'}
          </Text>
        </Td>

        <Td textAlign="right">
          <VaultsAction
            isOpen={openExpend}
            jumpVaultsDetail={() => {
              jumpVaultsDetail(apiInfo.vaultsId)
            }}
          />
        </Td>
      </Tr>

      {!openExpend && <Tr h="16px" />}

      <Tr
        cursor="pointer"
        position="relative"
        top="-16px"
        left="0px"
        sx={{
          td: {
            p: '0 !important',
            bg: 'transparent !important',
            border: 'none !important',
            _first: {
              borderRadius: ' 16px !important'
            },
            _last: {
              borderRadius: '0 0 16px 0 !important'
            }
          },
          _hover: {
            bg: 'transparent !important',
            td: {
              bg: 'transparent !important'
            }
          }
        }}
      >
        {openExpend && (
          <Td colSpan={6}>
            <Block p="16px" borderTop="none" borderRadius=" 0 0 16px 16px ">
              <VaultsExpendItem
                rewardPerA={rewardPerA}
                rewardPerB={rewardPerB}
                apiInfo={apiInfo}
                logo_url={logo_url}
                holdCoinAmounts={holdCoinAmounts}
                balanceDisplay={balanceDisplay}
                sharePoolRate={sharePoolRate}
              />
            </Block>
          </Td>
        )}
      </Tr>
    </React.Fragment>
  )
}
